import React, { useState, useEffect } from "react";
import Navbar from "../../components/navbar/navbar";
import "./liveshare.css";
// import { connect, sendMsg } from "../../helper/ws";
let timer = null;
// const API = "localhost:8080";

const API = "gonotesrizky.herokuapp.com";
// const API = "http://localhost:8080/api";

const Liveshare = (props) => {
  const [socket, setsocket] = useState(null);
  const [msg, setmsg] = useState(null);

  useEffect(() => {
    var socket = new WebSocket(`ws://${API}/ws/sassa`);
    setsocket(socket);
    socket.onopen = () => {
      console.log("Successfully Connected");
    };

    socket.onmessage = (msg) => {
      console.log(msg.data);

      var data = JSON.parse(msg.data);

      setmsg(data.body);
    };

    socket.onclose = (event) => {
      console.log("Socket Closed Connection: ", event);
    };

    socket.onerror = (error) => {
      console.log("Socket Error: ", error);
    };
  }, []);

  const send = (a) => {
    console.log("hello");
    socket.send(a);
    // sendMsg("hello");
  };

  const handleCheck = (newdata) => {
    // Clears running timer and starts a new one each time the user types
    clearTimeout(timer);
    timer = setTimeout(() => {
      // this.toggleCheck();
      delayedSave(newdata);
      console.log("fire fire fire");
    }, 300);
  };

  const delayedSave = (newdata) => {
    send(newdata);
  };

  return (
    <div className="darkmode is-fullheight ">
      <Navbar username={"username"} />

      <h1 className="title trashtitle">Liveshare</h1>
      {/* 
      {isloading && (
        <progress className="progress is-success loadingbar " max="100">
          Loading...
        </progress>
      )} */}

      <div className="columns is-centered is-fullheight">
        <div className="column trashtilecontinner is-four-fifths  is-8-mobile is-8-tablet is-8-desktop is-8-widescreen">
          <div className="livesharemaindiv">
            <div>
              <textarea
                id="textareanewnote"
                className="textarea inputbordercolor newnotediv darkmode"
                placeholder="What's on your mind"
                rows={msg ? msg.split(/\r\n|\r|\n/).length + 3 : 1}
                value={msg}
                // defaultValue={msg}

                onChange={(e) => {
                  handleCheck(e.target.value);
                  // send(e.target.value);
                  setmsg(e.target.value);
                }}
                // onChange={(e) => {
                //   setnewtodocontent(e.target.value);
                // }}
                name="todo"
              ></textarea>{" "}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Liveshare;
