import React, { useState, useEffect } from "react";
import Navbar from "../../components/navbar/navbar";
import axios from "axios";
import moments from "moment/moment";
import "./trash.css";
import Swal from "sweetalert2";

// const API = "http://localhost:8080/api";

const API = "https://gonotesrizky.herokuapp.com/api";

const Trash = (props) => {
  const [todos, settodos] = useState([]);
  const [isloading, setisloading] = useState(true);
  const [username, setusername] = useState("");
  useEffect(() => {
    var token = localStorage.getItem("jwt");

    if (!token) {
      props.history.push("/");
    }

    var config = {
      headers: { authorization: token },
    };
    axios
      .get(`${API}/gettrashall`, config)
      .then((result) => {
        setisloading(false);
        console.log(result.data);
        // setid(result.data.id);
        settodos(result.data.notes);

        setusername(result.data.username);
      })
      .catch((err) => {
        setisloading(false);
        console.log(err);
      });
  }, []);

  const deactivatenoe = (selectednoteid) => {
    Swal.fire({
      title: "Are you sure?",
      text: "Note will be restored",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, do it!",
    }).then((result) => {
      if (result.value) {
        var token = localStorage.getItem("jwt");

        var config = {
          headers: { authorization: token },
        };
        setisloading(true);

        var payload = {
          noteid: selectednoteid,
          state: true,
        };

        console.log(payload);

        axios
          .post("/api/changestate", payload, config)
          .then((result) => {
            console.log(result.data);
            setisloading(false);

            settodos((pre) => {
              var noteindex = pre
                .map(function (e) {
                  return e.id;
                })
                .indexOf(selectednoteid);
              console.log(noteindex);
              pre.splice(noteindex, 1);

              return [...pre];
            });

            Swal.fire("Restored!", "Your note is restored", "success");
          })
          .catch((err) => {
            console.log(err);
          });
      }
    });
  };

  return (
    <div className="darkmode ">
      <Navbar username={username} />

      <h1 className='title trashtitle' >Trash</h1>

      {isloading && (
        <progress className="progress is-success loadingbar " max="100">
          Loading...
        </progress>
      )}

      <div className="columns is-centered">
        <div className="column trashtilecontinner is-four-fifths  is-8-mobile is-8-tablet is-8-desktop is-8-widescreen">
          {todos.map((ele, index) => {
            return (
              <div className="tile is-ancestor  ">
                {/* <div className="tile is-vertical is-8 is-centered"> */}
                <div className="tile">
                  <div className="tile is-parent is-vertical notetileouter  ">
                    <article className="tile is-child notification is-primary notetiletrash">
                      <p className=" is-pulled-right">
                        Created : {moments(ele.created).fromNow()}
                      </p>
                      <br></br>
                      <p className="is-pulled-right ">
                        Updated : {moments(ele.updated).fromNow()}
                      </p>
                      <p className="title ">
                        {ele.encrypted ? "encrypted" : ele.title}
                      </p>
                      <p className="subtitle contentnote">
                        {ele.encrypted ? "encrypted" : ele.note}
                      </p>{" "}
                      <button
                        onClick={() => deactivatenoe(ele.id)}
                        className="button trashrestorbtn is-small is-danger"
                      >
                        Restore note
                      </button>
                    </article>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Trash;
